import {utils} from "./controllers/utils";
import {ArrowHead} from "./ArrowHead";

export function UmlRelation(props) {
    let relation = props.umlRelation;
    let fromUml = props.umlClasses[relation.from.classId], toUml = props.umlClasses[relation.to.classId];
    let [x1, y1] = utils.getPos(fromUml, relation.from);
    let [x2, y2] = utils.getPos(toUml, relation.to);

    return (
        <g className={props.mode !== utils.editTypes.Normal ? 'disable-select': ''}>
            <ArrowHead id={"arrowhead" + relation.id} mode={relation.type}/>
            <line className={"relation"}
                  relation_id={relation.id}
                  x1={x1} y1={y1} x2={x2} y2={y2}
                  x1_={x1} y1_={y1} x2_={x2} y2_={y2}
                  from_id={relation.from.classId}
                  to_id={relation.to.classId}
                  stroke="#000" strokeWidth="2"
                  markerEnd={`url(#arrowhead${relation.id})`}
                  strokeDasharray={utils.getStrokeDasharray(relation.type)}
            />
            <line className={"relation"}
                  relation_id={relation.id}
                  x1={x1} y1={y1} x2={x2} y2={y2}
                  x1_={x1} y1_={y1} x2_={x2} y2_={y2}
                  from_id={relation.from.classId}
                  to_id={relation.to.classId}
                  stroke="transparent" strokeWidth="30" onClick={() => { props.deleteRelation(relation.id)
            }}/>
        </g>
    )
}
