import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import {utils} from "./controllers/utils";
import {PatchPlus, XSquare} from "react-bootstrap-icons";
import Form from "react-bootstrap/Form";

export function EditRelation(props) {


    let handleClose = () => props.setShow(false);
    return (<Modal show={props.show} size="sm" style={{display: 'fixed'}} onHide={handleClose}>
        <Modal.Header closeButton> <Modal.Title>Delete Relationship</Modal.Title> </Modal.Header>
        <Modal.Body>
            Are you sure you want to delete this relationship?
        </Modal.Body>
        <Modal.Footer>
            <Button variant="outline-danger" onClick={() => {
                props.deleteRelation();
                handleClose();
            }}>Delete</Button>
            <Button variant="outline-secondary" onClick={handleClose}>Close</Button>
        </Modal.Footer>
    </Modal>)
}