import {useState} from "react";
import {umlClassesObj, umlRelationsObj} from "./controllers/init";
import {NavBar} from "./NavBar";
import $ from "jquery";
import {UmlClass} from "./UmlClass";
import {UmlRelation} from "./UmlRelation";
import {ClassEditor} from "./ClassEditor";
import {utils} from "./controllers/utils";
import {EditRelation} from "./EditRelation";

let fetchedModel = true;

export function Editor(props) {

    let [umlClasses, setUmlClasses] = useState({});
    let [umlRelations, setUmlRelations] = useState({});

    // let projectName = utils.getProjectName();

    // if (projectName === null) {
    //     fetch('/createProject')
    //         .then((response) => response.text())
    //         .then(project_id => {
    //             window.location.replace("?project=" + project_id);
    //         });
    // }

    // if (!fetchedModel) {
    //     function buildObj(ary, keyFunc) {
    //         let res = {};
    //         ary.forEach(function (val) {
    //             res[keyFunc(val)] = val;
    //         })
    //         return res
    //     }

    //     fetch('/getProject/' + projectName)
    //         .then((resp) => resp.json())
    //         .then(function(model){
    //             setUmlClasses(() => buildObj(model.umlClasses, (umlClass) => umlClass.id))
    //             setUmlRelations(() => buildObj(model.relationships, (relationship) => relationship.id))
    //         }.bind(this))
    //     fetchedModel = true;
    // }

    const [deleteSelectedRelation, setDeleteSelectedRelation] = useState(() => {})
    const [show, setShow] = useState(false);
    const [showRelation, setShowRelation] = useState(false);
    let [umlClass, setUmlClass] = useState({});
    let [mode, setMode] = useState(utils.editTypes.Normal)
    let [viewX, setViewX] = useState(800);
    let [viewY, setViewY] = useState(800);
    let [curRelation, setCurRelation] = useState([]);


    function editClassUml(id) {
        setUmlClass({...JSON.parse(JSON.stringify(umlClasses[id]))});
    }

    function updateUmlClass() {
        let updatedUmlClasses = {...umlClasses};
        updatedUmlClasses[umlClass.id] = JSON.parse(JSON.stringify(umlClass));
        setUmlClasses(updatedUmlClasses);
    }

    function deleteUmlClass(id) {
        let updatedUmlClasses = {...umlClasses};
        delete updatedUmlClasses[id]
        setUmlClasses(updatedUmlClasses);
    }

    function removeRedundantRelations() {
        let updatedUmlRelations = null;
        for (let id in umlRelations) {
            let umlRelation = umlRelations[id];
            if (!umlClasses.hasOwnProperty(umlRelation.from.classId) || !umlClasses.hasOwnProperty(umlRelation.to.classId)){
                if (updatedUmlRelations === null) {
                    updatedUmlRelations = {...umlRelations};
                }
                delete updatedUmlRelations[id];
            }
        }

        if (updatedUmlRelations !== null) {
            setUmlRelations(updatedUmlRelations);
        }
    }
    removeRedundantRelations();

    function addUmlClass() {
        let newUmlClass = {
            x: 100, y: 100, width: 180, className: 'ClassName', id: utils.rand(),
            fields: [{name: 'field', visibility: 'Default', id: utils.rand()}],
            methods: [{name: 'method', visibility: 'Default', id: utils.rand()}],
        }
        umlClasses = {...umlClasses}
        umlClasses[newUmlClass.id] = newUmlClass
        setUmlClasses(umlClasses);
        setMode(utils.editTypes.Normal);
        setTimeout(() => {
            let selected = $($(`#${newUmlClass.id}`)[0]);
            selected.parent().append(selected)
            selected.find('.rect').attr('stroke', 'red')
        }, 1000)

        editClassUml(newUmlClass.id)
        setShow(true);
    }

    function selectBorder(pos) {
        if (mode === utils.editTypes.Normal) {
            return
        }
        curRelation.push(pos)

        if (curRelation.length === 2) {
            let [fromLoc, toLoc] = curRelation;
            let updatedUmlRelations = {...umlRelations};
            let id = utils.rand();
            updatedUmlRelations[id] = {id: id, type: mode, update: 0, from:fromLoc, to: toLoc}
            setCurRelation([])

            if (fromLoc.classId !== toLoc.classId)
                setUmlRelations(updatedUmlRelations);
        }
    }
    
    function deleteRelation(id) {
        setDeleteSelectedRelation(() => {
            return function() {
                let updatedRelations = {...umlRelations};
                delete updatedRelations[id];
                setUmlRelations(updatedRelations);
            }
        })
        setShowRelation(true)
    }

    function setZoom(isZoomIn) {
        setViewX(viewX => {
            let newX = Math.max(500, viewX + (isZoomIn ? -100 : 100));
            return newX;
        });
        setViewY(viewY => Math.max(500, viewY + (isZoomIn ? -100 : 100)));
    }

    async function saveChanges() {
        $.post('setProject', {
            relationships: Object.values(umlRelations),
            umlClasses: Object.values(umlClasses),
        })
        let success = $('#save-successful');
        success.removeClass('hide');
        setTimeout(() => {success.addClass('hide')}, 1000 * 10)
    }

    return (
        <div id={"drawing-board-container"}>
            <NavBar mode={mode} setMode={setMode} addUmlClass={addUmlClass} setZoom={setZoom} saveChanges={saveChanges}/>
            <div className="alert alert-success fade show hide" role="alert" id={'save-successful'}>
                <strong>Congrats!</strong> The project is now saved and can be opened using <strong>{window.location.href}</strong>.
                Share link and collaborate with others!
            </div>
            <svg style={{height: 'calc(100% - 63px)', width: '100%'}} preserveAspectRatio={'xMinYMin meet'}
                 viewBox={`0 0 ${viewX} ${viewY}`} id={"drawing-board"}>
                <line id='cur-relation' x1={0} y1={0} x2={0} y2={0} strokeWidth={2} stroke={'green'}/>
                {/*<rect width={viewX} height={viewY} fill={"none"} stroke={"grey"} className={'svg-container'}/>*/}
                <g> {
                    Object.values(umlClasses).map(umlClass => (
                        <UmlClass key={umlClass.id}
                                  selectBorder={selectBorder}
                                  mode={mode} setMode={setMode}
                                  umlClasses={umlClasses} classDef={umlClass}
                                  setClasses={setUmlClasses} editClassUml={editClassUml}
                                  setShow={setShow}
                                  curRelation={curRelation} />))
                } </g>
                <g> {
                    Object.values(umlRelations).map(umlRelation => (
                        <UmlRelation key={umlRelation.id}
                                     mode={mode} setMode={setMode}
                                     umlRelation={umlRelation}
                                     umlClasses={umlClasses}
                                     deleteRelation={deleteRelation}/>))
                } </g>
            </svg>
            <ClassEditor umlClass={umlClass} setUmlClass={setUmlClass}
                         mode={mode} setMode={setMode}
                         show={show} setShow={setShow}
                         deleteUmlClass={deleteUmlClass}
                         updateUmlClass={updateUmlClass}
                         editClassUml={editClassUml}/>
            <EditRelation deleteRelation={deleteSelectedRelation}
                          setShow={setShowRelation}
                          show={showRelation}/>
        </div>
    )
}