import {utils} from "./controllers/utils";
import $ from "jquery";

export function UmlClass(props) {
    const uml = props.classDef;
    let umlClasses = {...props.umlClasses};
    const heightOfFields = utils.getFieldsHeight(uml)
    const height = utils.getUmlHeight(uml), width = uml.width;
    const defaultColor = '#a7d8f6';
    let borderWidth = 30;

    let selected = undefined, x, y;
    let startX, startY, relation_doms, timeStart, duration;

    function getNewPosition(e) {
        e.preventDefault();
        const [newX, newY] = utils.getMousePosition(e);
        return [startX + newX - x, startY + newY - y];
    }

    function dragStart(e) {
        e.preventDefault();
        if (props.mode !== utils.editTypes.Normal)
            return;

        selected = $(`#${uml.id}`);
        selected.parent().append(selected);
        selected.find('.rect').attr('stroke', '#56849e');

        [x, y] = utils.getMousePosition(e);
        [startX, startY] = [uml.x, uml.y];
        document.onmousemove = dragMove;
        document.onmouseup = dragDone;
        relation_doms = $(`.relation[from_id=${uml.id}], .relation[to_id=${uml.id}]`);
        timeStart = Math.round(Date.now());
    }

    function dragMove(e) {
        e.preventDefault();
        if (selected !== undefined) {
            let [newX, newY] = getNewPosition(e);
            selected.attr('transform', `translate(${newX}, ${newY})`)

            function updateRelation(relation, uml) {
                let [xAttr, yAttr] = uml.id === relation.attr('from_id') ? ['x1', 'y1'] : ['x2', 'y2'];
                let [xPos, yPos] = utils.getMousePosition(e);
                if (relation) {
                    let attrDiff = {};
                    attrDiff[xAttr] = parseFloat(relation.attr(xAttr + '_')) + xPos - x;
                    attrDiff[yAttr] = parseFloat(relation.attr(yAttr + '_')) + yPos - y;
                    relation.attr(attrDiff)
                }
            }

            relation_doms.each(function (_, relation) {
                updateRelation($(relation), uml)
            })

        }
    }

    function dragDone(e) {
        e.preventDefault();
        if (props.mode !== utils.editTypes.Normal)
            return;
        let classDom = selected;
        const id = classDom.attr('id');
        let [newX, newY] = getNewPosition(e);
        selected.find('.rect').attr('stroke', defaultColor);


        selected = undefined;
        document.onmousemove = null;
        document.onmouseup = null;

        duration = Math.round(Date.now()) - timeStart;
        if (duration < 200 && ((startX - newX) ** 2 + (startY - newY) ** 2) ** .5 < 5) {
            props.editClassUml(classDom.attr('id'));
            props.setShow(true);
        }

        umlClasses[id] = {...umlClasses[id], x: Math.round(newX), y: Math.round(newY)};
        props.setClasses(umlClasses);

    }

    function showBorderSelection(e, isShow) {
        let borderSelection = $($(e.target).closest('.uml-class').find('.border-selection'));
        if (isShow) {
            borderSelection.removeClass('hide');
        } else {
            borderSelection.addClass('hide');
        }
        return borderSelection;
    }

    function borderSelectionStart() {
        if (props.mode === utils.editTypes.Normal)
            return
        document.onmousemove = borderSelectionMove
    }

    function getClosestBorder(e) {
        let [x, y] = utils.getMousePosition(e);
        [x, y] = [x - uml.x, y - uml.y];
        let candidates = [[0, 0], [0, height], [width, 0], [width, height]];

        if (0 <= x && x <= width) {
            candidates.push([x, 0]);
            candidates.push([x, height]);
        }
        if (0 <= y && y <= height) {
            candidates.push([0, y]);
            candidates.push([width, y]);
        }

        let [closestX, closestY] = candidates[0];
        let getDist = (relX, relY) => ((relX - x) ** 2 + (relY - y) ** 2)
        for (let [candX, candY] of candidates) {
            if (getDist(closestX, closestY) > getDist(candX, candY))
                [closestX, closestY] = [candX, candY];
        }
        return [closestX, closestY];
    }

    function borderSelectionMove(e) {
        e.preventDefault();
        let borderSelection = showBorderSelection(e, true);
        let [closestX, closestY] = getClosestBorder(e)


        let [absX, absY] = [uml.x + closestX, uml.y + closestY];
        if (props.curRelation.length === 0) {
            $('#cur-relation').attr({x1: absX, y1: absY, x2: absX, y2: absY})
        } else if (props.curRelation.length === 1) {
            $('#cur-relation').attr({x2: absX, y2: absY})
        }
        borderSelection.attr({cx: closestX, cy: closestY});
    }

    function borderSelectionDone(e) {
        e.preventDefault();

        if (props.mode === utils.editTypes.Normal)
            return

        let curRelation = $('#cur-relation')
        curRelation.attr({x2: curRelation.attr('x1'), y2: curRelation.attr('y1')})

        document.onmousemove = null;
        showBorderSelection(e, false)
    }

    function setBorder(e) {
        e.preventDefault();
        let [closestX, closestY] = getClosestBorder(e)

        let dir = '', loc = -1;
        if (closestX === 0) {
            dir = 'left'
        } else if (closestY === 0) {
            dir = 'top'
            loc = closestX / width;
        } else if (closestX === width) {
            dir = 'right'
        } else if (closestY === height) {
            dir = 'down'
        }

        if (dir === 'left' || dir === 'right') {
            loc = closestY / height;
        } else if (dir === 'top' || dir === 'down') {
            loc = closestX / width;
        }

        props.selectBorder({classId: uml.id, dir: dir, loc: loc});
    }

    return (
        <g transform={`translate(${uml.x}, ${uml.y})`} id={uml.id} className={'uml-class unselectable'}>
            <rect fill={"white"} stroke={defaultColor} strokeWidth="3" width={width} height={height}
                  style={{pointerEvents: 'all'}} className={'rect'} onMouseDown={dragStart.bind($(this).parent())}/>
            <path d={`M 0 -${borderWidth/2} v ${height + borderWidth} M -${borderWidth/2} 0 h ${width + borderWidth} M ${width} -${borderWidth/2} v ${height + borderWidth} M -${borderWidth/2} ${height} h ${width + borderWidth}`}
                  stroke={'transparent'} strokeWidth={borderWidth} onMouseEnter={borderSelectionStart}
                  onMouseLeave={borderSelectionDone} onMouseUp={setBorder} className={props.mode === utils.editTypes.Normal ? 'disable-select': ''}/>
            <text x={width / 2} y={20} textAnchor="middle" fontWeight="bold" style={{pointerEvents: 'none'}}>{uml.className}</text>
            <path d={`M 0 30 h ${width} M 0 ${heightOfFields + 45} h ${width}`} stroke={defaultColor} strokeWidth="1"/>
            <text y="30" style={{pointerEvents: 'none'}}>{uml.fields.map(field => <tspan x={15} dy={25} key={field.id}>{utils.getVisibility(field.visibility) + field.name}</tspan>)}</text>
            <text y={heightOfFields + 45} style={{pointerEvents: 'none'}}>
                {uml.methods.map(className => <tspan x={15} dy={25} key={className.id}>{utils.getVisibility(className.visibility) + className.name}</tspan>)}</text>
            <circle cx="0" cy="0" r="12" stroke="#f6d8d5" strokeWidth="8" fill={'transparent'} style={{pointerEvents:'none'}} className={'hide border-selection unselectable'}/>

        </g>
    )
}