import Navbar from "react-bootstrap/Navbar";
import {Container, Nav, NavDropdown} from "react-bootstrap";
import Button from "react-bootstrap/Button";
import {Camera, Plus, PlusSquare, Save, Upload, XSquare, ZoomIn, ZoomOut} from "react-bootstrap-icons";
import {utils} from "./controllers/utils";
import {ArrowHead} from "./ArrowHead";
import html2canvas from 'html2canvas';
import $ from "jquery";

function NavItem(props) {
    let id = utils.rand();
    return (
        <NavDropdown.Item onClick={() => props.setMode(props.mode)}>
            <svg width={120} height={50}>
                <ArrowHead id={'arrowhead' + id} mode={props.mode} />
                <line className={"relation"}
                      x1={10} y1={10} x2={110} y2={10}
                      stroke="#000" strokeWidth="2"
                      markerEnd={`url(#arrowhead${id})`}
                      strokeDasharray={utils.getStrokeDasharray(props.mode)}
                />
                <text x={20} y={40}>{props.mode}</text>
            </svg>
        </NavDropdown.Item>
    )
}

export function NavBar(props) {
    function takeScreenshot() {
        html2canvas(document.body).then(function(canvas) {
            const a = document.createElement("a");
            a.href = canvas.toDataURL("image/jpeg");
            a.download = "uml_class_diagram.jpeg";
            a.click();
            $(a).remove();
        });
    }

    return (

        <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark" data-html2canvas-ignore="true">
            <Container>
                <Navbar.Brand href="#home">UmlEditor</Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="me-auto">
                        <NavDropdown title="+ Relationship">
                            <NavItem mode={utils.editTypes.Composition} setMode={props.setMode}/>
                            <NavDropdown.Divider/>
                            <NavItem mode={utils.editTypes.Aggregation} setMode={props.setMode}/>
                            <NavDropdown.Divider/>
                            <NavItem mode={utils.editTypes.Inheritance} setMode={props.setMode}/>
                            <NavDropdown.Divider/>
                            <NavItem mode={utils.editTypes.Realization} setMode={props.setMode}/>
                            <NavDropdown.Divider/>
                            <NavItem mode={utils.editTypes.Dependency} setMode={props.setMode}/>
                            <NavDropdown.Divider/>
                            <NavItem mode={utils.editTypes.Association} setMode={props.setMode}/>
                        </NavDropdown>
                        <Nav.Link onClick={props.addUmlClass}>+ Class</Nav.Link>
                        <Nav.Link><div className={props.mode === utils.editTypes.Normal? 'hide' : ''}><Button variant="outline-danger" size="md" onClick={() => props.setMode(utils.editTypes.Normal)}><XSquare className='menuIcon'/> Exit</Button></div></Nav.Link>

                    </Nav>
                    <Nav>
                        <Nav.Link><Button size="md" variant="outline-info" onClick={() => props.setZoom(true)}><ZoomIn className='menuIcon'/> Zoom In</Button></Nav.Link>
                        <Nav.Link><Button size="md" variant="outline-info" onClick={() => props.setZoom(false)}><ZoomOut className='menuIcon'/> Zoom out</Button></Nav.Link>
                        <Nav.Link><Button size="md" variant="outline-warning" onClick={() => { window.location.replace('/')}}><PlusSquare className='menuIcon'/> New</Button></Nav.Link>
                        <Nav.Link><Button size="md" variant="outline-warning" onClick={takeScreenshot}><Camera className='menuIcon'/> Screenshot</Button></Nav.Link>
                        <Nav.Link><Button size="md" variant="outline-warning" onClick={props.saveChanges}><Save className='menuIcon'/> Save</Button></Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    )
}