import {utils} from "./controllers/utils";

export function ArrowHead(props) {
    switch (props.mode) {
        case utils.editTypes.Composition:
            return (<marker id={props.id} markerWidth="20" markerHeight="20"
                            refX="14" refY="6"
                            orient="auto">
                <polygon points="2,6 8,2 15,6 8,10" fill={'black'} stroke={'black'}/>
            </marker>)
        case utils.editTypes.Aggregation:
            return (<marker id={props.id} markerWidth="20" markerHeight="20"
                            refX="14" refY="6"
                            orient="auto">
                <polygon points="2,6 8,2 15,6 8,10" fill={'white'} stroke={'black'}/>
            </marker>)

        case utils.editTypes.Inheritance:
            return (<marker id={props.id} markerWidth="20" markerHeight="14"
                            refX="12" refY="6" orient="auto">
                <polygon points="2,2 2,10 12,6" fill={'white'} stroke={'black'}/>
            </marker>)


        case utils.editTypes.Realization:
            return (<marker id={props.id} markerWidth="20" markerHeight="14"
                            refX="12" refY="6" orient="auto">
                <polygon points="2,2 2,10 12,6" fill={'white'} stroke={'black'}/>
            </marker>)

        case utils.editTypes.Dependency:
            return (<marker id={props.id} markerWidth="20" markerHeight="14"
                            refX="10" refY="5" orient="auto" fill={"transparent"} stroke={'black'}>
                <path d="M 0 0 L 10 5 L 0 10"/>
            </marker>)
        case utils.editTypes.Association:
            return (<marker id={props.id} markerWidth="20" markerHeight="14"
                            refX="10" refY="5" orient="auto" fill={"transparent"} stroke={'black'}>
                <path d="M 0 0 L 10 5 L 0 10"/>
            </marker>)    }
}