import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import {PatchPlus, XSquare} from "react-bootstrap-icons";
import Form from "react-bootstrap/Form";
import {utils} from "./controllers/utils";

export function ClassEditor(props) {
    let [show, umlClass, setUmlClass, updateUmlClass, deleteUmlClass] =
        [props.show, props.umlClass, props.setUmlClass, props.updateUmlClass, props.deleteUmlClass];

    let handleClose = () => {
        props.editClassUml(props.umlClass.id)
        props.setShow(false)
    };

    function onChangeName(evt) {
        setUmlClass({...umlClass, className: evt.target.value})
    }

    function onChangeFieldsMethods(evt, setter) {
        let newClass = {...JSON.parse(JSON.stringify(umlClass))};
        setter(newClass)
        setUmlClass(newClass)
    }

    function saveChanges(e) {
        e.preventDefault();
        updateUmlClass();
        handleClose();
    }

    function deleteClose(e) {
        e.preventDefault();
        deleteUmlClass(props.umlClass.id);
        handleClose();
    }

    return (<Modal show={show} size="lg" style={{display: 'fixed'}} onHide={handleClose}>
        <Modal.Header closeButton> <Modal.Title>Edit UML Class</Modal.Title> </Modal.Header>
        <Modal.Body>
            <h4>Class Name</h4>
            <input className="form-control form-control-lg" type="text" value={umlClass.className}
                   onChange={onChangeName}/>
            <br/>
            <h4>Fields <Button variant="outline-success" style={{float: 'right'}}
                               onClick={evt => onChangeFieldsMethods(evt, (cls) => {
                                   cls.fields.push({visibility: 'Default', name: 'field', id: utils.rand()})
                               })}>
                Add <PatchPlus size={20}/></Button></h4>
            <table className="table table-hover">
                <thead>
                <tr>
                    <th scope="col">Visibility</th>
                    <th scope="col">Field Definition</th>
                </tr>
                </thead>
                <tbody>
                {
                    (umlClass.fields || []).map((field, id) => (
                        <tr key={field.id}>
                            <td><Form.Select className="form-control" value={field.visibility} onChange={
                                evt => onChangeFieldsMethods(evt, (cls) => {
                                    cls.fields[id].visibility = evt.target.value
                                })}>
                                {Object.keys(utils.visibilities).map(
                                    (vis, i) => (<option value={vis} key={i}>{vis}</option>))}
                            </Form.Select></td>
                            <td><input className="form-control form-control" type="text" value={field.name}
                                       onChange={evt => onChangeFieldsMethods(evt, (cls) => {
                                           cls.fields[id].name = evt.target.value
                                       })}/></td>
                            <td style={{width: 20}}><XSquare className={"del-btn"} onClick={
                                evt => onChangeFieldsMethods(evt, (cls) => {
                                    cls.fields.splice(id, 1)
                                })}/></td>
                        </tr>))
                }
                </tbody>
            </table>
            <br/>
            <h4>Methods <Button variant="outline-success" style={{float: 'right'}} onClick={
                evt => onChangeFieldsMethods(evt, (cls) => {
                    cls.methods.push({visibility: 'Default', name: 'method', id: utils.rand()})
                })}>
                Add <PatchPlus size={20}/></Button></h4>
            <table className="table table-hover">
                <thead>
                <tr>
                    <th scope="col">Visibility</th>
                    <th scope="col">Method Signature</th>
                </tr>
                </thead>
                <tbody>
                {
                    (umlClass.methods || []).map((method, id) => (
                        <tr key={method.id}>
                            <td><Form.Select className="form-control" value={method.visibility} onChange={
                                evt => onChangeFieldsMethods(evt, (cls) => {
                                    cls.methods[id].visibility = evt.target.value
                                })}>
                                {Object.keys(utils.visibilities).map(
                                    (vis, i) => (<option value={vis} key={i}>{vis}</option>))}
                            </Form.Select></td>
                            <td><input className="form-control form-control" type="text" value={method.name}
                                       onChange={evt => onChangeFieldsMethods(evt, (cls) => {
                                           cls.methods[id].name = evt.target.value
                                       })}/></td>
                            <td style={{width: 20}}><XSquare className={"del-btn"} onClick={
                                evt => onChangeFieldsMethods(evt, (cls) => {
                                    cls.methods.splice(id, 1)
                                })}/></td>
                        </tr>))
                }
                </tbody>
            </table>
        </Modal.Body>
        <Modal.Footer>
            <Button variant="outline-danger" onClick={deleteClose}>Delete</Button>
            <Button variant="outline-secondary" onClick={handleClose}>Close</Button>
            <Button variant="outline-primary" onClick={saveChanges}>Save Changes</Button>
        </Modal.Footer>
    </Modal>)
}