import {utils} from "./utils";

let umlClassesObj = {}, umlRelationsObj = {};


function init() {
    const umlClass = {
        x: 0,
        y: 0,
        width: 180,
        className: 'Hello',
        fields: [{name: 'field1'}, {name: 'field2'}, {name: 'field3'}, {name: 'field1'}, {name: 'field2'}, {name: 'field3'}, {name: 'field1'}, {name: 'field2'}, {name: 'field3'}],
        methods: [{name: 'class1'}, {name: 'class2'}, {name: 'class3'}],
    }

    let xys = [[100, 100], [600, 400], [900, 200], [900, 900]];
    let ids = [];
    xys.forEach((xy) => {
        let [x, y] = xy;
        let id = utils.rand();
        umlClassesObj[id] = {...umlClass, x: x, y: y, id: id};
        ids.push(id)
    })

    for (const umlClass of Object.values(umlClassesObj)) {
        umlClass.fields = umlClass.fields.map((field) => {return {...field, id: utils.rand(), visibility: 'Private'}});
        umlClass.methods = umlClass.methods.map((field) => {return {...field, id: utils.rand(), visibility: 'Private'}});
    }

    const relations = [
        {
            from: {classId: ids[0],dir: 'right',loc: .50},
            to: {classId: ids[1], dir: 'left', loc: .60},
            type: "Dependency",
            update: 0
        },
        {
            from: {classId: ids[0], dir: 'right', loc: .50},
            to: {classId: ids[2], dir: 'left', loc: .4},
            type: "Inheritance",
            update: 0
        },
        {
            from: {classId: ids[3], dir: 'left', loc: .50},
            to: {classId: ids[0], dir: 'right', loc: 1},
            type: "Inheritance",
            update: 0
        }
    ]

    relations.forEach((relation) => {
        let id = utils.rand();
        umlRelationsObj[id] = {...relation, id: id}
    })
}
init();

umlClassesObj = {};
umlRelationsObj = {};

export{umlClassesObj, umlRelationsObj}
