import $ from "jquery";


function rand() { return String(Math.floor(Math.random() * 100000000000000000000)) }

function getFieldsHeight(uml) {
    return uml.fields.length * 25;
}

function getUmlHeight(uml) {
    return getFieldsHeight(uml) + uml.methods.length * 25 + 60;
}

function getMousePosition(e) {
    e.preventDefault();
    const ctm = $('#drawing-board')[0].getScreenCTM();
    return [(e.clientX - ctm.e) / ctm.a, (e.clientY - ctm.f) / ctm.d]
}

function getPos(uml, pos) {
    let x = uml.x, y = uml.y, width = uml.width, height = utils.getUmlHeight(uml), loc = pos.loc;
    return {
        'left': [x, y + height * loc],
        'right': [x + width, y + height * loc],
        'top': [x + width * loc, y],
        'down': [x + width * loc, y + height]
    }[pos.dir]
}


const editTypes = {
    Normal: 'Normal',
    Composition: 'Composition',
    Aggregation: 'Aggregation',
    Inheritance: 'Inheritance',
    Realization: 'Realization',
    Dependency: 'Dependency',
    Association: 'Association',
}

function getStrokeDasharray(editType) {
    return (editType === editTypes.Realization || editType === editTypes.Dependency) ? "8" : 0;
}

const visibilities = {
    Public: 'Public',
    Protected: 'Protected',
    Default: 'Default',
    Private: 'Private',
    Package: '~',
}

function getVisibility(visibility) {
    const display =
        {
            Default: '',
            Public: '+',
            Protected: '#',
            Private: '-',
            Package: '~',
        }[visibility];
    return display || '';
}

function getProjectName() {
    return new URLSearchParams(window.location.search).get('project')
    // return (window.location.href.match('/project/([^/]+)$') || [null, null]) [1]
}

export let utils = {
    getFieldsHeight: getFieldsHeight,
    getUmlHeight: getUmlHeight,
    getMousePosition: getMousePosition,
    getPos: getPos,
    editTypes: editTypes,
    rand: rand,
    getStrokeDasharray: getStrokeDasharray,
    visibilities: visibilities,
    getVisibility: getVisibility,
    getProjectName: getProjectName
}
